<template>
  <div class="container">
    <Row>
      <Col span="12" offset="3">
        <Input v-model="stock"></Input>
      </Col>
      <Col span="5" offset="2">
        <Button type="warning" @click="calculate" long>测算</Button>
      </Col>
    </Row>
    <div id="kline1" style="height: 500px;width: 100%;background:#ccc;margin-top: 10px"></div>
  </div>
</template>

<script>
import {RSI, MACD, OBV2, MA, OBV} from '@/tools/indicator.js'
import echarts from 'echarts'
import moment from 'moment'
import _ from 'lodash'
export default {
  data() {
    return {
      kline: {},
      stock: 'sh600519'
    }
  },
  mounted() {
    // this.getKline()
  },
  methods: {
    calculate() {
      this.getKline()
    },
    getKline() {
      return this.$http({
        url: 'http://money.finance.sina.com.cn/quotes_service/api/json_v2.php/CN_MarketData.getKLineData', 
        params: {
          symbol: this.stock,
          scale: 5,
          ma: 'no',
          datalen: 1000
        }
      }).then(res => {
        const data = eval(res.data)
        let kline = {
          t: [],
          o: [],
          h: [],
          l: [],
          c: [],
          v: []
        }
        data.forEach(it => {
          kline.t.push(moment(it.day).unix())
          kline.o.push(it.open)
          kline.h.push(it.high)
          kline.l.push(it.low)
          kline.c.push(it.close)
          kline.v.push(it.volume)
        })
        this.kline = kline
        this.initChart()
      })
    },
    getOBV2() {
      return OBV2(this.kline.o, this.kline.c, this.kline.h, this.kline.l, this.kline.v)
    },
    initChart(data) {
        const option = {
          title: {
              text: 'obv'
          },
          tooltip: {
              trigger: 'axis'
          },
          legend: {
              data:['kline','obv', 'indicator'],
              top: '3%'
          },
          grid: {
              top: '15%',
              left: '13%',
              right: '14%',
              bottom: '13%',
              // containLabel: true
          },
          dataZoom: [{
              type: 'inside',
              start: 0,
              end: 100
          }, {
              start: 0,
              end: 10,
              handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
              handleSize: '80%',
              handleStyle: {
                  color: '#fff',
                  shadowBlur: 3,
                  shadowColor: 'rgba(0, 0, 0, 0.6)',
                  shadowOffsetX: 2,
                  shadowOffsetY: 2
              }
          }],
          xAxis: {
              type: 'category',
              boundaryGap: false,
              data: this.kline.t.map(it => moment(it*1000).format('MM-DD HH:mm:ss'))
          },
          yAxis: [
            {
              name: 'kline',
              type: 'value',
              max: 'dataMax',
              min: 'dataMin',
              // max: _.max(this.kline.c),
              // min: _.min(this.kline.c),
            },
            {
                name: 'obv',
                max: 'dataMax',
                min: 'dataMin',
                type: 'value',
                show: false,
                // max: _.max(data),
                // min: _.min(data),
            },
            {
                name: 'indicator',
                max: 'dataMax',
                min: 'dataMin',
                type: 'value',
                show: false,
                // max: _.max(data),
                // min: _.min(data),
            }
          ],
          series: [
              {
                  name:'kline',
                  type:'line',
                  yAxisIndex:0,
                  data: this.kline.c
              },
              {
                  name:'obv',
                  type:'line',
                  yAxisIndex:1,
                  data: this.getOBV2()
              }
          ]
      };
      const kline1 = echarts.init(document.getElementById('kline1'));
      kline1.setOption(option);
    }
  }
}
</script>

<style scoped lang="less">
  .container {
    overflow: hidden;
    padding: 20px 5px;
    background-color: #333;
    min-height: 100%;
    color: #fff;
  }

</style>



